@import './Variables.scss';

.creator{
    height:auto;
    @include paddingMargin(0,0);
    background: radial-gradient(rgb(5, 63, 150),black);
    background-position: top;
    background-size: cover;
    animation: creator 0.8s linear;
        @keyframes creator {
            0%{
                opacity: 0;
            }
            100%{
                opacity: 1;
            }
        }
    .creator-title{
        @include maxWidthmaxHeight(85%,100px);
        @include paddingMargin(0,0 auto);
        @include displayFlex(row);
        @media(max-width:1200px){
            @include maxWidthmaxHeight(100%,100px);
        }
        @media(max-width:768px){
            @include displayFlex(column);
            @include maxWidthmaxHeight(100%,150px);
        }
        p{
            position: relative;
            @include maxWidthmaxHeight(30%,50%);
            @include top50;
            @include paddingMargin(5px,0);
            text-transform: uppercase;
            font-size: 1.5rem;
            @include colorFont;
            text-align: center;
            @media(max-width:768px){
                @include maxWidthmaxHeight(100%,50%);
                top:0%;
                transform: translateY(0);
                @include paddingMargin(10px,0);
            }
            @media(max-width:300px){
                font-size: 0.9rem;
            }
        }
        .creator-list{
            position: relative;
            @include maxWidthmaxHeight(70%,100%);
            @media(max-width:768px){
                @include maxWidthmaxHeight(100%,50%);
            }
            ul{
                @include maxWidthmaxHeight(100%,100%);
                @include displayFlex(row);
                list-style-type: none;
                justify-content: center;
                li{
                    @include maxWidthmaxHeight(auto,100%);
                    padding: 10px;
                    margin-left:10px;
                    margin-right:10px;
                        &:hover{
                            background: rgb(196, 0, 0);
                        }
                    P{
                        @include maxWidthmaxHeight(100%,auto);
                        font-size: 0.9rem;
                        color:rgb(231, 231, 231);
                        cursor: pointer;
                            @media(max-width:768px){
                                font-size: 0.8rem;
                            }
                            @media(max-width:300px){
                                font-size: 0.7rem;
                            }
                    }
                }
            }
        }
    }
    .creator-content{
        @include maxWidthmaxHeight(85%,auto);
        @include paddingMargin(0,0 auto);
        @include displayFlex(row);
        overflow: hidden;
        background: linear-gradient(to left,rgba(58, 58, 58, 0),rgba(0, 0, 0, 0.247));
        @media(max-width: 1300px){
            @include maxWidthmaxHeight(100%,auto);
        }
        @media(max-width: 1200px){
            flex-wrap: wrap;
        }
        .creator-pattern{
            position: relative;
            height: auto;
            @include paddingMargin(0,0);
            .color-pattern{
                @include maxWidthmaxHeight(100%,150px);
                @include displayFlex(column);
                justify-content: center;
                @media(max-width:992px){
                    @include maxWidthmaxHeight(50%,150px);
                    float: left;
                }
                @media(max-width:576px){
                    @include maxWidthmaxHeight(100%,100px);
                    display: block;
                }
                .color-title{
                    @include maxWidthmaxHeight(100%,30%);
                    p{
                        text-align: center;
                        color:rgb(223, 223, 223);
                        @include top50;
                        font-size: 1.1rem;
                        @media(max-width: 768px){
                            font-size: 1rem;
                        }
                    }
                }
                .color-elements{
                    @include maxWidthmaxHeight(100%,70%);
                    @include displayFlex(row);
                    justify-content: center;
                    flex-wrap: wrap;
                    @media(max-width:1200px){
                        flex-grow: 1;
                    }
                    .color-pattern-element{
                        position: relative;
                        top:30%;
                        @include maxWidthmaxHeight(40px,40px); 
                        margin: 2px;
                        cursor: pointer;
                        transition: all 0.2s;
                        box-shadow: 0 0 2px white;
                        @media(max-width:1200px){
                            @include maxWidthmaxHeight(30px,30px);
                        }
                    }
                }
            }
            .creator-pattern-sizes{
                position: relative;
                @include maxWidthmaxHeight(100%,150px);
                @include paddingMargin(0,0);
                @media(max-width:992px){
                    @include maxWidthmaxHeight(50%,150px);
                    float: left;
                }
                @media(max-width:576px){
                    @include maxWidthmaxHeight(100%,140px);
                    display: block;
                }
                p{
                    @include maxWidthmaxHeight(100%,30%); 
                    font-size: 1.1rem;
                    padding-top: 10px;
                    margin-bottom: 0;
                    text-align: center;
                    color:rgb(230, 230, 230);
                    @media(max-width: 768px){
                        font-size: 1rem;
                    }
                }
                ul{
                    @include maxWidthmaxHeight(100%,70%);
                    @include displayFlex(row);
                    @include paddingMargin(0,0);
                    justify-content: center;
                    position: relative;
                    list-style-type: none;  
                    li{
                        @include paddingMargin(0,2px);
                        position: relative;
                        top:50%;
                        transform: translateY(-50%);
                        width:60px;
                        height:50px;
                        border-bottom: 2px solid rgb(119, 119, 119);
                        border-left: 2px solid rgb(119, 119, 119);
                        border-radius: 0 0 0 10px;
                        &:hover{
                          cursor:pointer;
                          background-color: rgb(11, 50, 87);
                        }
                        @media(max-width: 992px){
                            @include maxWidthmaxHeight(50px,50px);  
                        }
                        p{  
                            background: none;
                            @include maxWidthmaxHeight(100%,100%);
                            color:rgb(53, 53, 53);
                            padding: 10px;
                            font-size: 1.2rem;
                            @media(max-width: 992px){
                                font-size: 1.1rem;  
                            }
                        }
                    }
                }
            }
            .theme-pattern-button{
                @include maxWidthmaxHeight(100%,auto);
                button{
                    @include maxWidthmaxHeight(130px,60px);
                    @include paddingMargin(0,10px auto);
                    border-left: 1px solid silver;
                    border-top: 1px solid silver;
                    color:silver;
                    outline: none;
                    text-transform: uppercase;
                    display: none;
                    background: none;
                    font-size: 0.8rem;
                    @media(max-width: 768px){
                        display:block;
                        @include maxWidthmaxHeight(130px,60px);
                    }
                }
            }
            .theme-pattern::-webkit-scrollbar{
                width: 2px;
                    @media(max-width: 768px){
                        height: 5px; 
                    }
            }
            .theme-pattern::-webkit-scrollbar-thumb{
                background: rgb(122, 122, 122);
                border-radius: 10px;
            }
            .theme-pattern{
                position: relative;
                @include maxWidthmaxHeight(100%,500px);
                overflow:scroll;
                @media(max-width:992px){
                    @include maxWidthmaxHeight(100%,300px);
                    text-align: center;
                }
                @media(max-width:768px){
                    @include maxWidthmaxHeight(100%,180px);
                    overflow-y: visible;
                    white-space: nowrap;
                }
                .theme-pattern-element{
                    @include maxWidthmaxHeight(100%,200px);
                    @include paddingMargin(0,5px auto);
                    color:white;
                    text-align: center;
                    border-bottom: 2px solid rgb(245, 245, 245);
                    cursor: pointer;
                    img{
                        @include top50;
                        width: 10rem;
                    }
                    @media(max-width: 992px){
                        @include maxWidthmaxHeight(30%,150px); 
                        display: inline-block;
                    }
                    @media(max-width: 768px){
                        @include top50;
                        @include maxWidthmaxHeight(50%,150px); 
                        @include paddingMargin(0,5px 10px)
                    }
                }
            }
        }
        .creator-draw{
            position: relative;
            height: auto;
            @include paddingMargin(0,0);
            @media(max-width:992px){
                @include maxWidthmaxHeight(100%,auto);
            }
            .tshirt{
                position: relative;
                @include maxWidthmaxHeight(500px,600px);
                margin-top:5%;
                transform: translateY(-5%);
                left:50%;
                transform: translateX(-50%);
                background: url('../img/tshirt.png');
                background-size: cover;
                background-position: center;
                border-radius: 50px;
                box-shadow: 0 5px 0 rgb(105, 105, 105);
                    @media(max-width:1100px){
                        @include maxWidthmaxHeight(450px,550px);
                    }
                    @media(max-width:800px){
                        @include maxWidthmaxHeight(450,550px);
                        position: relative;
                        margin-top:2%;
                        transform: translateY(-2%);
                        left:50%;
                        transform: translateX(-50%);
                    }
                    @media(max-width:576px){
                        @include maxWidthmaxHeight(90%,600px);
                    }
                    @media(max-width:450px){
                        @include maxWidthmaxHeight(100%,500px);
                    }
                    @media(max-width:350px){
                        @include maxWidthmaxHeight(95%,400px);
                    }
                    @media(max-width:300px){
                        @include maxWidthmaxHeight(100%,350px);
                    }
                .fas{
                    position: absolute;
                    color: green;
                    font-size: 5rem;
                    top:50%;
                    left:2%;
                }
                .tshirt-content{
                    position: absolute;
                    width: 40%;
                    height: 60%;
                    margin-top: 20%;
                    margin-left:50%;
                    transform: translateX(-50%);
                    border:1px solid black;
                    @media(max-width:576px){
                        width: 42%;
                    }
                    @media(max-width:350px){
                        width: 45%;
                    }
                    .tshirt-content-element{
                        position: relative;
                        @include maxWidthmaxHeight(100%,auto);
                        text-align: center;
                        cursor: move;
                        img{
                            @include maxWidthmaxHeight(100%,5%);
                        }
                    }
                }
            }
        }
        .creator-settings{
            position: relative;
            height:auto;
            border: 2px solid rgb(6, 50, 92);
            box-shadow: 0 0 10px rgb(6, 50, 92);
            @media(max-width:1200px){
                @include paddingMargin(0,0 auto);
            }
            p{
                width: 100%;
                text-align: center;
                padding:20px;
                @include colorFont;
                @media(max-width:576px){
                    padding:14px;
                }
            }
            .creator-sett-content{
                @include maxWidthmaxHeight(100%,100px);
                @include displayFlex(row);
                justify-content: center;
                flex-wrap: wrap;
                @media(max-width:992px){
                    @include paddingMargin(0,0 auto);
                    @include maxWidthmaxHeight(80%,100px);
                }
                button{
                    position: relative;
                    @include maxWidthmaxHeight(80px,80px);
                    @include paddingMargin(0,5px);
                    @include top50;
                    @include colorFont;
                    border:none;
                    font-size: 0.8rem;
                    background: none;
                    border: 1px solid rgb(235, 235, 235);
                    border-radius: 5px;
                    transition: 0.2s linear;
                    &:hover{
                        border: 1px solid rgb(13, 104, 146);
                    }
                    @media(max-width:1500px){
                        @include maxWidthmaxHeight(60px,60px);
                        font-size: 0.7rem;
                    }
                    .fas{
                        display: block;
                        @include paddingMargin(0,5px auto);
                        font-size: 1.5rem;
                        @media(max-width:1400px){
                            font-size: 1rem;
                        }
                    }
                }
            }
            .creator-summary{
                position: relative;
                @include maxWidthmaxHeight(100%,auto);
                @include displayFlex(column);
                @media(max-width:1200px){
                    @include displayFlex(row);
                }
                @media(max-width:576px){
                    flex-wrap: wrap;
                    justify-content: center;
                }
                @media(max-width:300px){
                    @include displayFlex(column);
                }
                .summary-title{
                    text-align: center;
                    @include paddingMargin(20px,0);
                }
                .summary-element{
                    @include maxWidthmaxHeight(100%,80px);
                    @include displayFlex(row);
                    @media(max-width:1200px){
                        @include displayFlex(column);
                        @include maxWidthmaxHeight(auto,200px);
                    }
                    @media(max-width:576px){
                        @include maxWidthmaxHeight(30%,200px);
                    }
                    @media(max-width:300px){
                        @include maxWidthmaxHeight(100%,150px);
                    }
                    .element-title{
                        @include maxWidthmaxHeight(50%,100%);
                        color:rgb(255, 255, 255);
                        text-align: center;
                        @media(max-width:1200px){
                            @include maxWidthmaxHeight(100%,auto);
                            top:0%;
                            transform: none;
                            text-align: center;
                        }
                        @media(max-width:576px){
                            font-size: 14px;
                        }
                        p{
                            @include top50;
                        }
                    }
                    .choosenColor{
                        @include maxWidthmaxHeight(50px,50px);
                        @include top50;
                        @include paddingMargin(0,0 auto);
                        border: 1px solid silver;
                        overflow: hidden;
                        @media(max-width:1200px){
                          top:20%;
                          transform: translateY(-20%);  
                        }
                    }
                    .choosenSize{
                        @extend .choosenColor;
                        text-align: center;
                        span{
                            color:rgb(240, 240, 240);
                            position: relative;
                            top:25%;
                            transform: translateY(-25%);
                        }
                    }
                    input{
                        @include maxWidthmaxHeight(50px,50px);
                        @include top50;
                        @include paddingMargin(0,0 auto);
                        color:rgb(240, 240, 240);
                        text-align: center;
                        background: none;
                        border: 1px solid silver;
                        @media(max-width:1200px){
                            top:20%;
                            transform: translateY(-20%);
                            @include paddingMargin(0,0 auto);
                        }
                    }
                    input::-webkit-outer-spin-button,
                    input::-webkit-inner-spin-button{
                        -webkit-appearance: none;
                    }
                    .price{
                        text-align: center;
                        font-size: 1.2rem;
                        color: aliceblue;
                        @media(max-width:1200px){
                            @include maxWidthmaxHeight(100%,auto);
                            top:20%;
                            transform: translateY(-20%);
                        }
                    }
                }
            }
            .buy{
                position: relative;
                @include maxWidthmaxHeight(150px,auto);
                @include paddingMargin(1rem,10px 50%);
                transform: translate(-50%);
                outline: none;
                background:none;
                color:rgb(240, 240, 240);
                font-size: 0.8rem;
                border: 1px solid rgb(7, 67, 116);
                &:hover{
                    @include fontCreator;
                }
            }
        }
    }
    .summary{
        position: absolute;
        padding: 0 !important;
        height:500px;
        left:50%;
        top:10%;
        transform: translate(-50%,-10%);
        background: rgb(255, 255, 255);
        background-size:cover;
        overflow: hidden;
        animation: summary 0.8s linear;
        z-index: 100;
        @keyframes summary{
            0%{
                opacity: 0;
            }
            100%{
                opacity: 1;
            }
        }
        @media(max-width:768px){
            height:450px;
        }
        .summary-title{
            @include maxWidthmaxHeight(100%,auto);
            position: relative;
            background: white;
            text-align: center;
            p{
                @include paddingMargin(20px,0);
                color:rgb(36, 36, 36);
            }
        }
        .summary-content{
            @include paddingMargin(0,0);
            @include maxWidthmaxHeight(100%,90%);
            @include displayFlex(row);
            position: relative;
            @media(max-width:500px){
                @include displayFlex(column); 
            }
            .summary-content-element{
                @include maxWidthmaxHeight(50%,100%);
                @include paddingMargin(0,0);
                overflow: hidden;
                display: block;
                background: none;
                &:first-child{
                    @media(max-width:500px){
                        @include maxWidthmaxHeight(100%,45%);
                    }
                }
                &:last-child{
                    background: radial-gradient(black,rgb(2, 34, 77));
                    @media(max-width:500px){
                        @include maxWidthmaxHeight(100%,55%);
                    }
                }
                .tshirt-summary{
                    @include paddingMargin(0,0 auto);
                    @include maxWidthmaxHeight(350px,400px);
                    background: url('../img/tshirt.png');
                    background-size: cover;
                    border:1px solid rgb(168, 168, 168);
                    @media(max-width:768px){
                        @include maxWidthmaxHeight(300px,350px);  
                    }
                    @media(max-width:576px){
                        @include maxWidthmaxHeight(250px,300px);  
                    }
                    @media(max-width:500px){
                        @include maxWidthmaxHeight(150px,200px);
                    }
                    .tshirt-summary-pattern{
                        @include maxWidthmaxHeight(40%,60%);
                        margin-top:20%;
                        margin-left: auto;
                        margin-right: auto;
                        img{
                            position: relative;
                            width:8rem;
                            left:50%;
                            transform:  translateX(-50%);
                        }
                    }
                }
                .summary-logo{
                    @include maxWidthmaxHeight(100%,auto);
                    @include paddingMargin(0,0 auto);
                    position: relative;
                    text-align: center;
                    margin-bottom: none !important;
                    @media(max-width:500px){
                        display: none;
                    }
                    img{
                        width: 10rem;
                    }
                }
                table{
                    @include maxWidthmaxHeight(80%,auto);
                    position: relative;
                    @include maxWidthmaxHeight(80%,auto);
                    @include paddingMargin(0,0 auto);
                    tr{
                        @include maxWidthmaxHeight(100%,60px); 
                        @include paddingMargin(0,0);
                        font-size: 1.2rem;
                        @media(max-width:768px){
                            @include maxWidthmaxHeight(50%,50px);
                            font-size: 0.8rem; 
                        }
                        @media(max-width:500px){
                            @include maxWidthmaxHeight(50%,40px); 
                        }
                        td{
                            @include paddingMargin(5px,0);
                            border-bottom: 1px solid silver;
                            text-align: left;
                            color:rgb(207, 207, 207);
                            &:first-child{
                                font-weight: 500;
                            }
                            &:last-child{
                                text-align: right;
                                position: relative;
                                right:20px;
                            }
                        }
                        .summary-color{
                            @include maxWidthmaxHeight(40px,40px);
                            position:absolute;
                            right: 0;
                            top:50;
                            transform: translateY(-50%);
                            clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
                            @media(max-width:768px){
                                @include maxWidthmaxHeight(30px,30px); 
                            }
                        }
                    }
                }
                .summary-buttons{
                    @include maxWidthmaxHeight(100%,auto);
                    padding: 0;
                    margin-top: 5px;
                    text-align: center;
                    button{
                        @include paddingMargin(0,0 auto);
                        @include maxWidthmaxHeight(50%,50px); 
                        text-transform: uppercase; 
                        outline: none;
                        border: none;
                        background: none;
                        font-size: 0.8rem;
                        color:white;
                        @media(max-width:450px){
                            font-size: 0.6rem;
                        }
                        &:hover{
                            @include fontCreator;
                        }
                    } 
                }
            }
        }
    }
    .instruction{
        @include maxWidthmaxHeight(700px,auto);
        @include displayFlex(row);
        top:0%;
        left:50%;
        transform: translateX(-50%);
        position: absolute;
        background: radial-gradient(rgb(153, 0, 0),rgb(0, 0, 0));
        display: none;
        .instruction-tshirt{
            @include maxWidthmaxHeight(55%,500px);
            border:1px solid black; 
            background: url('../img/tshirt.png');
            background-size:cover;
            background-position:center;
            .fa-arrows-alt-v{
                transform: translate(-50%,-50%);
                left:50%;
                top:50%;
                position: relative;
                font-size: 6rem;
                color:rgb(219, 219, 219);
            }
            .fa-arrows-alt{
                position: relative;
                font-size: 5rem;
                color:rgb(219, 219, 219);
                transform: translate(-50%,-20%);
                left:50%;
                top:20%;
            }
        }
        .instruction-tshirt-info{
            @include maxWidthmaxHeight(45%,500px);

        }
    }
}

